import React, { Component } from "react";
import { Router, Switch, Route } from "react-router";
import { createBrowserHistory } from "history";
// import { Home } from "./Home";
import {DataTable} from './Files';
import {Upload} from './Upload';
import {CreatableAdvanced} from './Folders';
import {Archiv} from './Archiv';
import {Err} from './Err';
import {OverlayExample} from './Loader';


const history = createBrowserHistory();



// import logo from './logo.svg';
// import './App.css';

export class App extends Component {
  render(){
  return (
    <div>
    <Router history={history}>
        <Switch>
          {/* <Route exact path="/">
            <Home history={history} />
          </Route> */}
          <Route exact path="/:userName">
            <DataTable history={history} />
          </Route>
          <Route exact path="/:userName/upload">
          <Upload history={history} />
          </Route>
          <Route exact path="/:userName/archiv">
          <Archiv history={history} />
          </Route>
          <Route exact path="/:userName/folders">
          <CreatableAdvanced history={history} />
          </Route>
          <Route exact path="/:userName/loader">
          <OverlayExample history={history} />
          </Route>
          <Route>
          <Err history={history} />
          </Route>
          
          </Switch>
      </Router></div>
      
  );
}}

export default App;
