full=()=>{
  console.log("in full")
  var elem = this.imgRef.current

  if (elem.requestFullscreen) {
    elem.requestFullscreen();
  } else if (elem.webkitRequestFullscreen) { /* Safari */
    elem.webkitRequestFullscreen();
  } else if (elem.msRequestFullscreen) { /* IE11 */
    elem.msRequestFullscreen();
  }
}

saveNotes=()=>{
console.log("in saveNote");

const file= this.state.selectedFile
var note = this.textAreaNoteRef.current.value;
var date = new Date()
var dd = String(date.getDate()).padStart(2, '0');
var mm = String(date.getMonth() + 1).padStart(2, '0'); 
var yyyy = date.getFullYear();
date = yyyy + '-' + mm + '-' +dd ;
console.log(date);
var editor=file.userName
const fileId = file._id
let uId = localStorage.getItem("uId");
console.log(note);
console.log(fileId);
$.ajax({
  type: "POST",
  url: "https://files.codes/api/"
   + localStorage.getItem('userName')
    + "/saveNotes",
  headers: { Authorization: this.state.jwtFromCookie },
  // mimeType: "multipart/form-data",
  data: JSON.stringify({ "notes": {"note":note,"date":date,"editor":editor}, fileId: fileId }),
  dataType: "json",
  contentType: "application/json",
  success:  (data)=> {
    console.log("save note! ", data);
    this.loadFiles()
      this.showPreFile(data.data)
      this.textAreaNoteRef.current.value=""
      
     
    
   
    
  },
});

}

